<template>
  <div>
    <!-- 导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
        <el-row>
            <el-col>
                <el-button type="primary" @click="showAddCateDialog">添加分类</el-button>
            </el-col>
        </el-row>
        <!-- 表格 -->
        <tree-table class="treetable" :data="catelist" :columns="columns" :selection-type="false" :expand-type="false"
        show-index border :show-row-hover="false">
            <!-- 是否有效 -->
            <template slot="isok" slot-scope="scope">
                <i class="el-icon-success" v-if="scope.row.cat_deleted===false" style="color:lightgreen;"></i>
                <i class="el-icon-error" v-else style="color:red;"></i>
            </template>
            <!-- 排序 -->
            <template slot="order" slot-scope="scope">
                <el-tag size="mini" v-if="scope.row.cat_level===0">一级</el-tag>
                <el-tag type="success" size="mini" v-else-if="scope.row.cat_level===1">二级</el-tag>
                <el-tag type="warning" size="mini" v-else-if="scope.row.cat_level===2">三级</el-tag>
            </template>
            <!-- 操作 -->
            <template slot="opt" slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.cat_id)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCatesById(scope.row.cat_id)">删除</el-button>
            </template>
        </tree-table>
        <!-- 分页区域 -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page.sync="queryInfo.pagenum" :page-sizes="[3,5,10,15]" :page-size="queryInfo.pagesize" 
        layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
    </el-card>
    <!-- 添加分类的对话框 -->
    <el-dialog title="添加角色" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClosed">
        <!-- 添加分类的表单 -->
        <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="80px">
            <el-form-item label="分类名称" prop="cat_name">
                <el-input v-model="addCateForm.cat_name"></el-input>
            </el-form-item>
            <el-form-item label="父级分类">
                <!-- option指定数据源 -->
                <!-- props指定配置对象 -->
                <el-cascader v-model="selectedKeys" :options="parentCateList" 
                :props="cascaderProps" @change="parentCateChanged"></el-cascader>
            </el-form-item>
        </el-form>
        <!-- 底部区域 -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="addCateDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addCate">确 定</el-button>
        </span>
    </el-dialog>
    <!-- 修改分类的对话框 -->
    <el-dialog title="修改分类" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <!-- 内容主题区域 -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
            <el-form-item label="名称" prop="cat_name">
                <el-input v-model="editForm.cat_name"></el-input>
            </el-form-item>
        </el-form>
        <!-- 底部区域 -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="editUser">确 定</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
    data()
    {
        return {
            //查询条件
            queryInfo:
            {
                type:3,
                pagenum:1,
                pagesize:5  
            },
            //商品分类的数据列表，默认为空
            catelist:[],
            //总数据条数
            total:0,
            //为table指定列的定义
            columns:
            [
                {
                    label:'分类名称',
                    prop:'cat_name'
                },
                {
                    label:'是否有效',
                    //表示将当前列定义为模板列
                    type:'template',
                    //表示当前这一列的使用名称
                    template:'isok'
                },
                {
                    label:'排序',
                    //表示将当前列定义为模板列
                    type:'template',
                    //表示当前这一列的使用名称
                    template:'order'
                },
                {
                    label:'操作',
                    //表示将当前列定义为模板列
                    type:'template',
                    //表示当前这一列的使用名称
                    template:'opt'
                }
            ],
            //控制添加分类对话框的显示与隐藏
            addCateDialogVisible:false,
            //添加分类的表单控制对象
            addCateForm:
            {
                //将要添加分类的名称
                cat_name:'',
                //父级分类的id
                cat_pid:0,
                //分类的等级，默认添加为1级分类（也即是0）
                cat_level:0
            },
            //添加分类表单的验证规则对象
            addCateFormRules:
            {
                cat_name: 
                [
                    { required: true, message: '请输入分类名称', trigger: 'blur' }
                ]
            },
            //父级分类的列表
            parentCateList:[],
            //指定级联选择器的配置对象
            cascaderProps:
            {
                expandTrigger: 'hover',
                value:'cat_id',
                label:'cat_name',
                children:'children'
            },
            //选中父级分类的id数据
            selectedKeys:[],
            editDialogVisible:false,
            editForm:{},
            editFormRules:
            {
                cat_name: 
                [
                    { required: true, message: '请输入分类名称', trigger: 'blur' }
                ]
            },
        }
    },
    created()
    {
        this.getCateList();
    },
    methods:
    {
        //获取商品分类数据
        async getCateList()
        {
            const {data:res}=await this.$https.get('categories',{params:this.queryInfo});
            if(res.meta.status!==200)
            {
                return this.$message.error('获取商品分类失败！');
            }
            //数据列表赋值给catelist
            this.catelist=res.data.result;
            //为总数据条数赋值
            this.total=res.data.total;
        },
        //监听pagesize改变的事件
        handleSizeChange(newSize)
        {
            this.queryInfo.pagesize=newSize;
            this.getCateList();
        },
        //监听页码值改变的事件
        handleCurrentChange(newPage)
        {
            this.queryInfo.pagenum=newPage;
            this.getCateList();
        },
        //点击按钮显示分类的对话框
        showAddCateDialog()
        {
            //先获取父级分类的数据列表再显示对话框
            this.getParentCateList();
            this.addCateDialogVisible=true;
        },
        //获取父级分类的数据列表
        async getParentCateList()
        {
            const {data:res}=await this.$https.get('categories',{params:{type:2}});
            if(res.meta.status!==200)
            {
                return this.$message.error('获取父级分类失败！');
            }
            this.parentCateList=res.data;
        },
        //选择项变化触发这个函数
        parentCateChanged()
        {
            //选中数组长度大于0意味着选中，否则没选中
            if(this.selectedKeys.length>0)
            {
                //父级分类的id
                this.addCateForm.cat_pid=this.selectedKeys[this.selectedKeys.length-1];
                //为当前的等级赋值
                this.addCateForm.cat_level=this.selectedKeys.length;
            }
            else
            {
                //父级分类的id
                this.addCateForm.cat_pid=0;
                //为当前的等级赋值
                this.addCateForm.cat_level=0;
            }
        },
        //点击按钮添加新的分类
        addCate()
        {
            this.$refs.addCateFormRef.validate(async valid=>
            {
                if(!valid)
                {
                    return ;
                }
                const{data:res}=await this.$https.post('categories',this.addCateForm);
                if(res.meta.status!==201)
                {
                    return this.$message.error('添加分类失败！');
                }
                this.$message.success('添加分类成功！');
                this.getCateList();
                this.addCateDialogVisible=false;
            });
        },
        //监听表单的关闭事件，重置表单数据
        addCateDialogClosed()
        {
            this.$refs.addCateFormRef.resetFields();
            this.selectedKeys=[];
            this.addCateForm.cat_pid=0;
            this.addCateForm.cat_level=0;
        },
        async showEditDialog(id)
        {
            const {data:res}=await this.$https.get('categories/'+id);
            if(res.meta.status!==200)
            {
                return this.$message.error('获取分类失败！');
            }
            this.editForm=res.data;
            this.editDialogVisible=true;
        },
        editDialogClosed()
        {
            this.$refs.editFormRef.resetFields();
        },
        editUser()
        {
            this.$refs.editFormRef.validate(async valid=>
            {
                if(!valid)
                {
                    return ;
                }
                console.log(this.editForm);
                const{data:res}=await this.$https.put('categories/'+this.editForm.cat_id,{cat_name:this.editForm.cat_name});
                if(res.meta.status!==200)
                {
                    return this.$message.error('修改分类失败！');
                }
                this.$message.success('修改分类成功！');
                this.getCateList();
                this.editDialogVisible=false;
            });
        },
        async removeCatesById(id)
        {
            const confirmResult=await this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).catch(err=>err);
            if(confirmResult!=='confirm')
            {
                return this.$message.info('已取消删除');
            }
            const {data:res}=await this.$https.delete('categories/'+id);
            if(res.meta.status!==200)
            {
                this.$message.error('删除分类失败！');
            }
            this.$message.success('删除分类成功！');
            this.getCateList();
        }
    }
}
</script>

<style lang="less" scoped>
.treetable {
    margin-top: 15px;
}
.el-cascader {
    width: 100%;
}
</style>